<template>
  <div>
    <input type="text" v-model="txt" class="qrcTxt">
    <button @click="start">刷新</button>
    <div id="qrcode"></div>
  </div>
</template>

<script>
  import $ from '../sudoku/jquery-1.8.0.min'
  import qrcode from './jquery.qrcode.min'
  export default {
    name: "Qrcode",
    data(){
      return {
        txt:'https://loolife.gitee.io/a_webpage_written_by_a_novice/'
      }
    },
    methods:{
      start(){
        let txt=this.txt;
        document.getElementById("qrcode").innerHTML='';
        jQuery(function(){
          jQuery('#qrcode').qrcode(txt); })
      }
    },
    mounted() {
      this.start();
    }
  }
</script>

<style scoped>
.qrcTxt{
  width: 380px;
}
</style>
